  border-bottom: 1px solid black;
}

.over-auto {
  overflow: auto;
}

.form-contain {
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
}

.form {
  margin: 70px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-error {
  color: red;
}

.tooltip:hover {
  box-shadow: -10px 10px 3px grey;
}

.spark-container {
  display: flex;
  flex-wrap: wrap;
}

.profile-image {
  width: 100%;
  height: 100%;
}

.paper {
  transition: 0.5s;
  margin-top: 15px;
}

.paper:hover {
  box-shadow: -10px 10px 3px grey;
}

.delete-icon {
  position: absolute;
  left: 25%;
}

.comment-form {
  border-bottom: 1px solid rgba(0,0,0,0.4);
}

.chat-form {
  position: fixed;
  bottom: 0;
  width: 97%;
  margin: 10px 20px 0px 20px;
  padding-bottom: 10px;
  background-color: #fff;
}

.chat-container {
  padding: 20px 30px 50px 30px;
}



